@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap");
.meeting-info-container{
    display: flex;
    flex-direction: column;
}

.appointment-card {
    background-color: aliceblue;;
    border-radius: 16px;
	width: 100%;
    margin: 1.5%;
    border: 1px solid;
    display: flex;
    flex-direction: row;
    position: relative;
}
.meeting-info {
    padding: 0 2.5% 0 2.5%;
    width: 30%;
    font-family: "DM Sans", sans-serif;
    display: flex;
    flex-direction: column;
}

    .purpose{
        font-size: 18px;
        font-weight: 700;
    }
    .date{
        font-size: 8px;
        font-weight: 700;
    }
    .duration{
        font-size: 8px;
        font-weight: 700;
    }

.zoom-info {
    padding: 0 5% 0 5%;
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: left;
}
    .zoom-username {
        padding: 5px 0 0 0;
    }
    .join-url {
        padding: 5px 0 0 0;

    }

.zoom-username, .join-url, .url{
    font-size: 12px;
    font-family: 'Raleway', sans-serif;
}

.button-container{
    display: flex;
    flex-direction: column;
    align-content: space-between;
    width: 7.5%;
    padding: 0 2.5% 0 2.5%;
}

.start-button{
    font-size: 12px;
    width: 50px;
    padding: 3px 3px;
    border: 0;
    background-color: #a3bdee;
    color: rgb(0, 0, 0);
    border-radius: 3px;
}

.email-button{
    font-size: 12px;
    width: 50px;
    padding: 3px 3px;
    border: 0;
    background-color: #d8d8d8;
    color: rgb(0, 0, 0);
    border-radius: 3px;
}

.delete-button{
    font-size: 12px;
    padding: 3px 3px;
    width: 50px;
    border: 0;
    background-color: #e7a7a7;
    color: rgb(0, 0, 0);
    border-radius: 3px;

}

