@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=DM+Sans:400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-item{
    font-family: 'Raleway', sans-serif;
    display: flex;
    color: inherit;
    cursor: pointer;
    opacity: 0.8;
    padding: 0 15px;
}

.navbar-item:hover {
    opacity: 1;
}
header {
    background-color: aliceblue;
    display: flex;
    height: 5vh;
    justify-content: space-between;
    align-items: center;
    color: rgba(0, 0, 0, 0.671);
    padding: 0 15px;
}

.navItems {
    display: flex;
}

header h1 {
    text-align: center;
}

header li {
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
    padding: 0 15px;
}

    header a:hover {
        opacity: 1;
    }

header nav {
    display: flex;
}

header nav {
    display: flex;
}

    header nav li:first-child {
        margin-left: 0;
    }

    header nav li:last-child {
        margin-right: 0;
    }

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input-fields {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    display: flex;
    flex-direction: column;
}
.login-input-field {
    margin-top: 6px;
    min-width: 18em;
    height: 30px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.log-in-button {
    font-size: 21px;
    padding: 5px 20px;
    border: 0;
    background-color: #3968be;
    color: #fff;
    border-radius: 3px;
};


.select-container {
    padding: 1px;
}

ul { 
    list-style-type: disc; 
    list-style-position: inside; 
    padding: 10px;
    margin: 0px;
}

.select-options {
    margin-top: 6px;
    min-width: 12em;
    height: 20px;
    font-size: 15px;
    font-family: 'Raleway', sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 10px;
    transition: all 250ms ease-in-out;
}

.select-container {
    display: flex;
    flex-direction: column;
}
.meeting-info-container{
    display: flex;
    flex-direction: column;
}

.appointment-card {
    background-color: aliceblue;;
    border-radius: 16px;
	width: 100%;
    margin: 1.5%;
    border: 1px solid;
    display: flex;
    flex-direction: row;
    position: relative;
}
.meeting-info {
    padding: 0 2.5% 0 2.5%;
    width: 30%;
    font-family: "DM Sans", sans-serif;
    display: flex;
    flex-direction: column;
}

    .purpose{
        font-size: 18px;
        font-weight: 700;
    }
    .date{
        font-size: 8px;
        font-weight: 700;
    }
    .duration{
        font-size: 8px;
        font-weight: 700;
    }

.zoom-info {
    padding: 0 5% 0 5%;
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: left;
}
    .zoom-username {
        padding: 5px 0 0 0;
    }
    .join-url {
        padding: 5px 0 0 0;

    }

.zoom-username, .join-url, .url{
    font-size: 12px;
    font-family: 'Raleway', sans-serif;
}

.button-container{
    display: flex;
    flex-direction: column;
    align-content: space-between;
    width: 7.5%;
    padding: 0 2.5% 0 2.5%;
}

.start-button{
    font-size: 12px;
    width: 50px;
    padding: 3px 3px;
    border: 0;
    background-color: #a3bdee;
    color: rgb(0, 0, 0);
    border-radius: 3px;
}

.email-button{
    font-size: 12px;
    width: 50px;
    padding: 3px 3px;
    border: 0;
    background-color: #d8d8d8;
    color: rgb(0, 0, 0);
    border-radius: 3px;
}

.delete-button{
    font-size: 12px;
    padding: 3px 3px;
    width: 50px;
    border: 0;
    background-color: #e7a7a7;
    color: rgb(0, 0, 0);
    border-radius: 3px;

}


.appointment-list-container {
  
  position: absolute;
}

.appointment-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
  }
