}


:root {
    --app-container: aliceblue;
    --app-background: aliceblue;
    --main-color: #1f1c2e;
    --secondary-color: #4A4A4A;
    --link-color: #1f1c2e;
    --link-color-hover: #c3cff4;
    --link-color-active: #fff;
    --link-color-active-bg: #1f1c2e;
    --projects-section: #fff;
    --list-bg: #fff;
    --list-bg-hover:  #f6fbff;
    --button-bg: #1f1c24;
    --search-area-bg: #fff;
    --star: #1ff1c2e;
    --message-btn: #fff;
}


.app_container {
    background-color: aliceblue;
    background-color: var(--app-container);
    width: 100%;
    height: 95vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.left_container {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0% 30px 0% 0%
}

    .explanation{
        font-family: "DM Sans", sans-serif;
        
        width: 90%;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
        .block_title{
            font-size: 20px;
            font-weight: 700;
        }
        .blick_date{
            font-size: 20px;
        }

    .appointment_display {
        height: 80vh;
        width: 100%;
        background-color: white;
        border-radius: 32px;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .appointment_list_container {
        flex-shrink: 0;
        padding-bottom: 32px;
        background-color: #fff;
        background-color: var(--projects-section);
        flex: 1 1;
        height: 60vh;
        width: 90%;
        border-radius: 30px;
        position: relative;
        overflow-y: auto;
        transition: all 300ms cubic-bezier(0.19, 1, 0.56, 1);
    }

.right_container {
    font-family: "DM Sans", sans-serif;
    background-color: white;
    border-radius: 32px;
    height: 80vh;
    width: 30%;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0% 0% 0% 0%;
}
.right-title {
    font-size: 20px;
    font-weight: 700;
    padding: 10px;
}
    .appointment_form {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px;
        width: 100%;
        height: 100%;
        overflow: auto;
        position: relative;
    }




