.recurrence-options{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.tab-container {
    display: flex;
    flex-direction: row;
}

.recurrence-menu {
    display: flex;
    flex-direction: column;
}

.submit-button {
    font-size: 20px;
    padding: 5px 5px;
    border: 0;
    background-color: #3968be;
    color: #fff;
    border-radius: 3px;
    position: absolute;
    bottom: 0;
}
@media (max-width: 1000px) {
    .left_container {
        width: 90%;
        height: 500px;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-top: 2px solid black;
        margin: 0 0 5% 0
    }
        .appointment_display {
            height: 450px;
        }

    .right_container {
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5% 0 0 0
    }

    .app_container {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }

    .EmailShare {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }


}



html, body {
    height: 100%;
}
